<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <!-- <div class="page-header">
        <div class="page-block">
          <div class="row align-items-center">
            <div class="col-md-6">
              <div class="page-header-title">
                <h5 class="m-b-10">Partes Procesales</h5>
              </div>
              <ul class="breadcrumb">
                <li class="breadcrumb-item">
                  <router-link to="/sisge">Inicio</router-link>
                </li>
                <li class="breadcrumb-item">Demandados</li>
              </ul>
            </div>
          </div>
        </div>
      </div> -->
      <div class="row mb-4">
        <h5 class="text-dark fw-bold">Partes Procesales</h5>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-header">
              <h5 class="text-secondary">Lista de Demandados</h5>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="miTabla1">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Documento</th>
                      <th>Nombre / Razon Social</th>
                      <th>Direccion</th>
                      <th>Telefono</th>
                      <th>Email</th>
                      <th>Opciones</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(persona, index) in personas" :key="index">
                      <td>
                      <div class="circle">{{ persona.nombre.charAt(0) }}</div>
                      </td>
                      <td>{{ persona.documento }}</td>
                      <td>{{ persona.nombre }}</td>
                      <td>{{ persona.direccion }}</td>
                      <td>{{ persona.telefono }}</td>
                      <td>{{ persona.correo }}</td>
                      <td> 
                        <router-link :to="{ name: 'historial/registro-demandado', params: { doc: persona.documento } }"
                          class="btn btn-primary py-1 px-2 mx-1 rounded">
                          <span class=""><i data-feather="edit"></i></span>
                        </router-link>
                        <router-link :to="{ name: 'demandado/informacion', params: { doc: persona.documento } }" class="btn btn-primary py-1 px-2 mx-1 rounded">
                          <span class=""><i data-feather="file-text"></i></span>
                        </router-link>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import feather from 'feather-icons';

import { mapActions } from 'vuex';
export default {
  name: 'expedienteAll',
  created() {
    this.listarDemandantes();
  },
  data() {
    return {
      demandantes: [],
      dataTable: null,
    };
  },
  computed: {
    personas() {
      const personasMap = new Map();

      this.demandantes.forEach((demandante) => {
        const key = demandante.tipo_persona === 'natural'
          ? demandante.nat_dni
          : demandante.jur_ruc;

        if (!personasMap.has(key)) {
          personasMap.set(key, {
            documento: key,
            nombre: demandante.tipo_persona === 'natural'
              ? `${demandante.nat_apellido_paterno} ${demandante.nat_apellido_materno} ${demandante.nat_nombres}`
              : demandante.jur_razon_social,
            direccion: demandante.dir_calle_av,
            telefono: demandante.tipo_persona === 'natural'
              ? demandante.nat_telefono
              : demandante.jur_telefono,
            correo: demandante.tipo_persona === 'natural'
              ? demandante.nat_correo
              : demandante.jur_correo,
            exp_id: demandante.exp_id,
          });
        }
      });

      return Array.from(personasMap.values());
    },
  },
  methods: {
    ...mapActions(['get', 'post']),
    listarDemandantes() {
      this.get({
        url: this.$store.getters.get__url + '/demandado',
        token: this.$store.getters.get__token,
      })
        .then((response) => {
          console.log(response);
          this.demandantes = response.data;
          this.initDataTable();
        })
        .catch((errors) => { });
    },
    initDataTable() {
      if (this.dataTable) {
        this.dataTable.destroy();
      }

      // Inicializamos DataTable con los datos
      this.$nextTick(() => {
        this.dataTable = $('#miTabla1').DataTable({
          language: {
            url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
          },
        });
        feather.replace(); // Recargar los iconos
      });
    },
    badgeClass(estado) {
      return {
        'badge bg-warning px-2 py-2': estado === 'EN TRAMITE',
        'badge bg-success px-2 py-2': estado === 'EN EJECUCION',
      };
    },
  },
  beforeDestroy() {
    if (this.dataTable) {
      this.dataTable.destroy();
    }
  },
};
</script>

<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word;
}

.circle {
  width: 30px;
  height: 30px;
  background-color: #7267EF;
  color: white;
  font-weight: 600;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
}
</style>
